import Vue from 'vue';
import Vuex from 'vuex';
import createPersistedState from 'vuex-persistedstate';
import user from './UserModule.js';
import search from './SearchModule.js';

Vue.use(Vuex);

export default new Vuex.Store({
  modules:{
    user,
    search,
  },
  plugins:[
    createPersistedState({
      paths:['user','search'],
    })
  ]
});
